.project {
    width: 350px;
    background-color: #64807a;
    display: flex;
    flex-direction: column;
}
.imgContainer {
    width: 100%;
    position: relative;
}
.imgContainer .projectImg {
    height: 200px;
    width: 100%;
    background-color: #ef8080;
}
.imgContainer .button {
    position: absolute;
    top: 50%;
    left: 30%;
    text-decoration: none;
    user-select: none;
    background: #605e5e;
    padding: 10px 25px;
}

.imgContainer .button:hover {
    background: #888484;
}
.imgContainer .button:active {
    background: #4a4a4a;
}

.title {
    margin: 5px 5px 0px 5px;
    background-color: #ffc47b;
}
.description {
    margin: 5px;
    background-color: #ffc47b;
}